.main-container #letterhead-container #down-arrow{background-image:url(data:image/png;base64,);width:5rem;height:2rem;background-size:contain;background-repeat:no-repeat;position:relative;-webkit-filter:brightness(2);filter:brightness(2);-webkit-animation:1.2s ease infinite bounce;animation:1.2s ease infinite bounce;cursor:pointer}#timeline{color:#56EFF9;margin:3rem;font-family:'Patrick Hand', cursive}@-webkit-keyframes bounce{0%{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}50%{-webkit-transform:translate3d(0, 1rem, 0);transform:translate3d(0, 1rem, 0)}100%{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}}@keyframes bounce{0%{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}50%{-webkit-transform:translate3d(0, 1rem, 0);transform:translate3d(0, 1rem, 0)}100%{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}}@-webkit-keyframes pulse{0%{opacity:0.5;-webkit-filter:blur(4px);filter:blur(4px);scale:0.4}50%{opacity:0.7;-webkit-filter:brightness(1) blur(2px);filter:brightness(1) blur(2px);box-shadow:5px 10px;scale:1}100%{opacity:0.3;-webkit-filter:blur(4px);filter:blur(4px);scale:0.4}}@keyframes pulse{0%{opacity:0.5;-webkit-filter:blur(4px);filter:blur(4px);scale:0.4}50%{opacity:0.7;-webkit-filter:brightness(1) blur(2px);filter:brightness(1) blur(2px);box-shadow:5px 10px;scale:1}100%{opacity:0.3;-webkit-filter:blur(4px);filter:blur(4px);scale:0.4}}@media screen and (max-width: 1300px){.main-container #letterhead-container h3{font-size:7rem}#timeline{font-size:1.6rem}}@media screen and (max-width: 1200px){.main-container h4{font-size:2rem}.main-container #letterhead-container h3{font-size:8rem}.main-container #letterhead-container #down-arrow{margin-top:5rem;width:4rem;height:1rem}}@media screen and (max-width: 1000px){.main-container h4{font-size:1rem}.main-container #letterhead-container h3{font-size:6rem}.main-container #letterhead-container #down-arrow{width:4rem;height:1rem}.main-co